import React from 'react';
import { classNames } from '../../../../shared/lib/classNames/classNames';
import styles from './TableHeader.module.scss';

export const TableHeader = () => {
    return (
        <div className={styles.header}>
            <span className={classNames(styles.text, {}, [styles.type])}></span>
            <span className={classNames(styles.text, {}, [styles.time])}></span>
            <span className={classNames(styles.text, {}, [styles.employee])}></span>
            <span className={classNames(styles.text, {}, [styles.call])}></span>
            <span className={classNames(styles.text, {}, [styles.source])}></span>
            <span className={classNames(styles.text, {}, [styles.rate])}></span>
            <span className={classNames(styles.text, {}, [])}></span>
        </div>
    );
};
